<template>
  <div class="container" v-if="isLoggedIn">
    <h1></h1>
    <!--  -->
    <div>
      <div class="form-group">
        <label for="goodName"></label>
        <input type="text" v-model="goodName" placeholder="" required>
        <span v-if="errors.goodName" class="error">{{ errors.goodName }}</span>
      </div>
      <div class="form-group">
        <label for="price"></label>
        <input type="number" v-model="price" placeholder="" required>
        <span v-if="errors.price" class="error">{{ errors.price }}</span>
      </div>

      <div class="form-group">
        <label for="stock"></label>
        <input type="number" v-model="stock" placeholder="" required>
        <span v-if="errors.stock" class="error">{{ errors.stock }}</span>
      </div>

      <div class="form-group">
        <label for="description"></label>
        <input type="text" v-model="description" placeholder="" required>
        <span v-if="errors.description" class="error">{{ errors.description }}</span>
      </div>
      <div class="form-group">
          <label for="kind"></label>
          <select v-model="selectedKind" @change="updateSubcategories">
            <option v-for="kind in kinds" :key="kind.value" :value="kind.value">{{ kind.text }}</option>
          </select>
      </div>
      <div class="form-group">
        <label for="subkind"></label>
        <select v-model="selectedSubkind">
          <option v-for="subkind in subkinds" :key="subkind" :value="subkind">{{ subkind }}</option>
        </select>
      </div>
      <label for="kind"></label>
      <div id="preview">
          <!--  -->
          <div v-if="selectedFiles.length > 0">
            <div v-for="(file, index) in selectedFiles" :key="index" v-show="index === currentPreviewIndex">
              <img v-if="isImage(file)" :src="getURL(file)" alt="">
              <video v-else controls :src="getURL(file)"></video>
              <button @click="removeFile(index)">X</button>
            </div>
          </div>
          <div v-else>
            
          </div>
      </div>
      <button type="button" @click="prevImage"></button>&nbsp;
      <button type="button" @click="nextImage"></button>

      <!-- /-->
      <input type="file" :disabled="isFileInputDisabled" ref="fileInput" @change="handleFileChange" accept="image/png, image/jpeg, video/mp4" multiple style="display: none;">
      <!--  -->
      <button type="button" @click="triggerFileInput">/</button>
      <!--  -->
      <span class="error" v-if="errors.pictureError">{{ errors.pictureError }}</span><br>
      <button @click="addProduct"></button>
    </div>

    <!--  -->
    <table>
      <thead>
        <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(product, index) in productList" :key="index">
        <td>{{ product.goodName }}</td>
        <td>{{ product.price }}</td>
        <td>{{ product.stock }}</td>
        <td>{{ product.description }}</td>
        <td>
          <div v-for="(file, fileIndex) in product.mediaFiles" :key="fileIndex">
            <img v-if="isImage(file)" :src="getURL(file)" alt="Image view" style="max-width: 100px; max-height: 100px; object-fit: contain;">
            <video v-else controls :src="getURL(file)" style="max-width: 100px; max-height: 100px;"></video>
          </div>
        </td>
        <td>{{ product.kind }}</td>
        <td>{{ product.subkind }}</td>
        </tr>
      </tbody>
    </table>
    <!--  -->
    <button @click="submitProducts"></button>
  </div>
  <div v-else>
    <a href="/login"></a>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      products: [], // 
      goodName: '',
      price: '',
      stock: '',
      description: '',
      selectedFiles: [],
      selectedKind: '',  // 
      selectedSubkind: '',       // mounted
      currentPreviewIndex: 0,    // 
      kinds: [
        { value: '', text: '' },
        { value: '', text: '' },
        { value: '', text: '' }
      ],
      subkinds: [],
      productList: [],
      errors: {
        pictureError: '',
        goodName: '',
        price: '',
        stock: '',
        description: ''
      },
      formData: new FormData(),
      currentUser:null,
    };
  },
  created() {
    this.fetchUsrFromSession();
  },
  mounted() {
    // 
    this.updateSubcategories();
  },
  computed: {
    isLoggedIn() {
      // 
      return !!this.currentUser;
    },
    currentFile() {
      return this.selectedFiles[this.currentPreviewIndex];
    },
    isFileInputDisabled() {
      return this.selectedFiles.length >= 3;
    },
  },
  methods: {
    async fetchUsrFromSession() {
      try {
        //  GET 
        const response = await axios.get('/now-usr');
        // 
        const usr = response.data;
        //  currentUser 
        this.currentUser = usr;

        return true;
      } catch (error) {
        console.error(':', error);
        return false;
      }
    },
    addProduct() {
      // 
      // 
      if (this.goodName.length > 20) {
        alert("20");
        return;
      }

      // 
      if (isNaN(this.price)) {
        alert("");
        return;
      }

      // 
      if (isNaN(this.stock)) {
        this.errors.stock = "";
        return;
      }

      // 
      if (this.description.length > 100) {
        alert("100");
        return;
      }

      // 
      if (this.selectedFiles.length === 0) {
        alert("");
        return;
      }

      // 
      const newProduct = {
        goodName: this.goodName,
        price: this.price,
        stock: this.stock,
        description: this.description,
        kind: this.selectedKind,
        subkind: this.selectedSubkind
      };

      // 
      this.productList.push(newProduct);

      //  FormData 
      this.selectedFiles.forEach((file, index) => {
        this.formData.append(`mediaFiles-${this.goodName}-${index}`, file);
      });

      // 
      this.goodName = '';
      this.price = '';
      this.stock = '';
      this.description = '';
      this.selectedFiles = [];

      alert(""); // 
    },
    isImage(file) {
      return file && file.type && file.type.startsWith('image/');
    },
    getURL(file){
      return URL.createObjectURL(file);
    },
    updateSubcategories() {
      // selectedKindsubkinds
      if (this.selectedKind === '') {
          this.subkinds = ['', ''];
        } else if (this.selectedKind === '') {
          this.subkinds = ['', '', ''];
        } else if (this.selectedKind === '') {
          this.subkinds = ['', '', '', ''];
        }
        // 
        if (this.subkinds.length > 0) {
          this.selectedSubkind = this.subkinds[0];
        } else {
          this.selectedSubkind = '';
      }
    },
    handleFileChange(event) {
      const files = Array.from(event.target.files);

      // 
      const validTypes = ['image/png', 'image/jpeg', 'video/mp4'];
      const validFiles = files.filter(file => validTypes.includes(file.type));

      // 
      if (validFiles.length < files.length) {
        alert("pngjpgmp4");
        return;
      }

      // selectedFiles3
      const remainingSlots = 3 - this.selectedFiles.length;
      const newFiles = validFiles.slice(0, remainingSlots);
      const maxFiles = 3;
      const maxSize = 10485760; // 10MB limit

      // 
      if (files.length > maxFiles) {
        alert(` ${maxFiles} `);
        return;
      }

      // 
      const isAnyFileTooLarge = files.some(file => file.size > maxSize);
      if (isAnyFileTooLarge) {
        alert("10MB");
      return;
      }
      
      // 3
      if (this.selectedFiles.length + newFiles.length > 3) {
        alert("3");
        return;
      }


      // selectedFiles3
      this.selectedFiles = [...this.selectedFiles, ...validFiles];

      // input file
      if (this.selectedFiles.length === 3) {
        event.target.disabled = true;
      } else {
        event.target.disabled = false;
        event.target.value = ''; // input file
      }

      //  input 
      if (this.$refs.fileInput) {
        this.$refs.fileInput.value = '';
      }

      // 3inputdisabled
      if (this.selectedFiles.length < 3) {
        event.target.disabled = false;
      }
    },
    goBack() {
      this.$router.go(-1);
    },
    removeFile(index) {
      URL.revokeObjectURL(this.selectedFiles[index]); // URL
      this.selectedFiles.splice(index, 1); // 

      // 3input
      if (this.selectedFiles.length < 3 && this.$refs.fileInput) {
        this.$refs.fileInput.disabled = false;
      }
      // inputvalue
      if (this.$refs.fileInput) {
        this.$refs.fileInput.value = '';
      }
    },
    triggerFileInput() {
      //  ref 
      if (this.$refs.fileInput) {
        this.$refs.fileInput.click();
      } else {
        console.error('');
      }
    },
    async submitProducts() {
        // FormData  data()

      // 
      this.productList.forEach(product => {
          //  FormData 
        this.formData.append('goodName', product.goodName);
        this.formData.append('description', product.description);
        this.formData.append('price', product.price);
        this.formData.append('number', product.stock);
        this.formData.append('kind', product.kind);
        this.formData.append('subkind', product.subkind);
          // 
        });
      try {
        //  fetch API  POST 
        const response = await fetch('/good/upload-multiple-goods', {
          method: 'POST',
          body: this.formData
        });
        this.productList = []; // 

        // 
        const responseData = await response.json();

        if (responseData.page === 'error') {
          // 
          this.$router.push({ path: '/error', query: { err: responseData.msg, to: responseData.data }})
        } else if (responseData.page === 'success') {
          // 
          this.$router.push({ path: '/success', query: { message: responseData.msg, to: responseData.data }})
        } else if (responseData.page === 'upload-multiplegoods') {// 
          this.$router.push({ name: 'UploadMultipleGoods' });
        } else if (responseData.page === 'upload-good') {
          this.$router.push({ name: 'UploadOneGood' });
        } else {
          // 
          console.log("");
        }
      } catch (error) {
        // 
        console.error(':', error);
        alert('');
      }
    },
    prevImage() {
      // 
      if (this.selectedFiles.length > 0) {
        this.currentPreviewIndex = (this.currentPreviewIndex - 1 + this.selectedFiles.length) % this.selectedFiles.length;
      }
    },
    nextImage() {
      // 
      if (this.selectedFiles.length > 0) {
        this.currentPreviewIndex = (this.currentPreviewIndex + 1) % this.selectedFiles.length;
      }
    }
    // 
  },

};
</script>

<style scoped>
        .container {
            max-width: 600px;
            margin: 0 auto;
            padding: 20px;
            background-color: #f5f5f5;
            border: 1px solid #ddd;
            border-radius: 5px;
            overflow-y: auto;
        }
        h1 {
            color: #333;
            text-align: center;
        }
        label {
            display: block;
            margin-bottom: 5px;
            font-weight: bold;
        }
        input[type="text"],
        input[type="file"] {
            width: 50%;
            padding: 10px;
            margin: 5px 0;
            border: 1px solid #ccc;
            border-radius: 3px;
        }
        button {
            background-color: #007BFF;
            color: #fff;
            border: none;
            padding: 10px 20px;
            border-radius: 3px;
            cursor: pointer;
        }
        table {
            width: 100%;
            border-collapse: collapse;
            margin-top: 20px;
        }
        
        th, td {
            padding: 10px;
            text-align: left;
            border: 1px solid #dddd; /*  */
        }
        
        th {
            background-color: #007BFF;
            color: #fff;
        }
        img {
            max-width: 100px;
            max-height: 100px;
             object-fit: contain;
        }
        
        .close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
    cursor: pointer;
}
.close:hover {
    color: #000;
    text-decoration: none;
}
    .close:focus {
        color: black;
        text-decoration: none;
        cursor: pointer;
    }
    
    .price-modal-content {
    background-image: url('~@/assets/img/a.jpg'); /*  */
    background-size: cover; /*  */
    background-position: center; /*  */
    opacity: 1; /* 0.880% */
}
#preview {
    /*  */
    width: 200px;
    height: 200px;
    border: 1px solid #ccc;
    position: relative;
    overflow: hidden;
}

#preview img {
    /*  */
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

#preview button {
    /*  */
    position: absolute;
    top: 0;
    right: 0;
    background: rgba(255, 255, 255, 0.5);
    border: none;

}
</style>
