<template>
  <div>
    <ul class="daohang">
      <div class="title-container">
        <h2 class="registration-title"></h2>
        <router-link to="/"></router-link>
      </div>
    </ul>
    
    <div class="login" style="top:53%;">
      <h2 v-if="isSeller"></h2>
      <h2 v-else></h2>

      <form @submit.prevent="handleRegister">
        <input type="hidden" v-model="form.userType">
<!--id-->
        <div class="input">
          <label for="newusername" style="top: -30px; font-size: 20px; font-weight: bold;"></label>
          <input id="newusername" type="text" v-model="form.username" required>
        </div>
        <div class="input">
          <label for="newpwd" style="top: -30px; font-size: 20px; font-weight: bold;"></label>
          <input id="newpwd" type="password" v-model="form.password" required>
        </div>
<!---->
        <div class="input">
          <label for="newquestion" style="top: -30px; font-size: 20px; font-weight: bold;"></label>
          <input id="newquestion" type="text" v-model="form.question" required>
        </div>
        <div class="input">
          <label for="newanswer" style="top: -30px; font-size: 20px; font-weight: bold;"></label>
          <input id="newanswer" type="text" v-model="form.answer" required>
        </div>

        <!--  -->
        <div class="input">
          <label></label>
          <div>
            <input type="radio" id="buyer" value="0" v-model="form.userType" @change="updateUserType('0')" > 
            <input type="radio" id="seller" value="1" v-model="form.userType" @change="updateUserType('1')" required> 
          </div>
        </div>

<!--        -->
        <div class="input" v-if="!isSeller" >
          <label for="newaddress" style="top: -30px; font-size: 20px; font-weight: bold;"></label>
          <input id="newaddress" type="text" v-model="form.address" required>
        </div>
        <div class="input" v-if="!isSeller">
          <label for="newphone" style="top: -30px; font-size: 20px; font-weight: bold;"></label>
          <input id="newphone" type="text" v-model="form.phone" required>
        </div>

        <div class="image-buttons" style="margin-bottom: 25px;">
          <input class="input-image-button" type="image" :src="require('@/assets/img/register.png')" alt="" title="" name="action" value=""/>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';


export default {
  data() {
    return {
      isSeller: true,
      form: {
        power: '1',
        username: 'user',
        phone: '1234567890',
        address: '',
        password: 'user',
        question: '',
        answer: '11'
      }
    };
  },
  methods: {
    async handleRegister(){
      try{
        const credentials = {
          username: this.form.username,
          password: this.form.password,
          question: this.form.question,
          answer: this.form.answer,
          power: this.form.userType,
          address: this.form.address,
          phone: this.form.phone

        };

        const response = await fetch('/usr/register-control', {
          method: 'POST',
          body: JSON.stringify(credentials),
          headers: {
            'Content-Type': 'application/json'
          }
        });
        const responseData = await response.json();

        if (responseData.msg === 'error') {
          console.log(responseData.msg); // 
        }else if (responseData.msg === 'rename'){
          alert(responseData.msg);
        } else if (responseData.msg === 'success') {
          console.log("success");
          console.log(responseData.msg);
          alert("Register Success!");
          this.$router.push('/'); // 
        }
      }catch (error){
        alert("Register fail!");
        // this.error = 'register fail!';
      }
    },
    updateUserType(userType) {
      this.isSeller = userType === '1';
      this.form.userType = userType;
    }
  },
  created() {
    this.isSeller = this.$route.query.registeruser === '1';
  },

};
</script>

