      <!--  -->
      <div class="right">
        <div class="centered-container">
          <h2></h2>
        </div>
        <div class="centered-container">
          <form @submit.prevent="handleSearch">
            <input type="text" v-model="searchQuery" class="custom-input" placeholder="">&nbsp;&nbsp;&nbsp;
            <select v-model="selectedCategory">
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select><br><br>&nbsp;&nbsp;&nbsp;
            <input type="submit" value="" style="width:130px;cursor: pointer;">
          </form>
          <div id="search_list"></div>
        </div><br>
          <div class="goods">
            <div class="show-1" v-for="item in paginatedItems" :key="item.id">
              <div class="media-container">
                  <div v-for="(media, index) in item.mediaFiles" :key="index" v-show="media.isActive">
                    <img v-if="!isVideo(media)" :src="media.url" alt="" v-show="media.isActive">
                    <video v-else :src="media.url" controls v-show="media.isActive"></video>
                  </div>
