                <p>{{ item.name }}<br>{{ item.price }}</p>
              </a>
            </div>
          </div>
          <div class="pagination">
            <button @click="goToPrevPage" class="prev" :disabled="currentPage === 1"></button>
            <span id="page-info"> {{ currentPage }} / {{ totalPages }} </span>
            <button @click="goToNextPage" class="next" :disabled="currentPage === totalPages"></button>
          </div>
