</body>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      items: [ ],
     searchQuery: '',
     selectedCategory: '', // 
     filteredItems: [], // 
      currentPage: 1,
      itemsPerPage: 2,
      search: {
        keyword: '',
        kind: ''
      },
      currentUser :{}
    };
  },


  computed: {
    isLoggedIn() {
      // 
      return !!this.currentUser;
    },
    isSeller() {
      // 
      return this.currentUser && this.currentUser.power === 1;
    },
    // 
    isBuyer() {
      // 
      return this.currentUser && this.currentUser.power === 0;
    },
    getUsername() {
      // 
      return this.currentUser ? this.currentUser.username : '';
    },
    paginatedItems() {
      // 
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = this.currentPage * this.itemsPerPage;
      return this.filteredItems.slice(start, end);
    },
    // 
    totalPages() {
      return Math.ceil(this.filteredItems.length / this.itemsPerPage);
    }
  },
  created() {
    this.fetchUsrFromSession();
  },
  methods: {
    async fetchUsrFromSession() {
      try {
        //  GET 
        const response = await axios.get('/now-usr');

        // 
        const usr = response.data;

        //  currentUser 
        this.currentUser = usr;
        console.log(this.currentUser.userid)
        return true;
      } catch (error) {
        console.error(':', error);
        return false;
      }
    },
    //api
    addproduct(goodid) {
      // 
      axios.post('/good/create-cart', { goodid: goodid,userid:this.currentUser.userid }).then(res => {
        // 
        console.log(res.data);
        alert("");
      }).catch(error => {
        // 
        console.error(error);
      });
    },
    async fetchgoodListSession() {
      try {
        //  GET 
        const goodsResponse = await axios.get('/good/buyer-all-good-list-controll');
        // 
        // console.log('goodList:', goodsResponse);
        const goodList = goodsResponse.data.data;//goodsResponsedata
        //  products 
        // picturemediaFiles
        //  console.log('goodList:', goodList);

        this.goods = goodList.map( good => {
          //    console.log('Before trimming:', good.picture); // 
          const trimmedPicture = good.picture.trim();
          //     console.log('After trimming:', trimmedPicture); // 
          const paths = trimmedPicture.split(',');
          const mediaFiles = paths.map((path, i) => {
            return {
              url: path,
              isActive: i === 0 // truefalse
            };
          });
          return {
            ...good,
            mediaFiles,
            // 
            goodname: good.goodname.trim(),
            description: good.description.trim(),
            price: good.price,
            number: good.number,
            kind: good.kind,
            subkind: good.subkind
          };
        });
        console.log('this.goods:', this.goods);
        return true;
      } catch (error) {
        console.error(':', error);
        return false;
      }
    },
    isVideo(media) {
      //
      return media.url.endsWith('.mp4') || media.url.endsWith('.avi');
    },
    async handleLogout() {
      try {
        // 
        await axios.get('/logout-control');
        // 
        this.$router.push('/');
      } catch (error) {
        console.error(':', error);
        // 
      }
    },
    navigateTo(routeName) {
      this.$router.push({ name: routeName });
    },
    beforeMount() {
      if (!this.isLoggedIn) {
        this.$router.push('/');
      }
    },
    handleSearch() {
      this.filteredItems = this.items.filter(item => {
          const matchesKeyword = this.searchQuery ? item.name.toLowerCase().includes(this.searchQuery.toLowerCase()) : true;
          const matchesCategory = this.selectedCategory ? item.kind === this.selectedCategory : true;

          console.log(`Item: ${item.name}, Keyword Match: ${matchesKeyword}, Category Match: ${matchesCategory}`);

          return matchesKeyword && matchesCategory;
      });
      console.log("Filtered items count:", this.filteredItems.length);
      // 
    },
    showPrevMedia(item) {
        const currentIndex = item.mediaFiles.findIndex(media => media.isActive);
        console.log('Current active index:', currentIndex);

        if (currentIndex >= 0) {
        const prevIndex = (currentIndex - 1 + item.mediaFiles.length) % item.mediaFiles.length;
        console.log('Previous index:', prevIndex);
        this.setActiveMedia(item, prevIndex);
        }
    },
    showNextMedia(item) {
        const currentIndex = item.mediaFiles.findIndex(media => media.isActive);
        console.log('Current active index:', currentIndex);

        if (currentIndex >= 0) {
        const nextIndex = (currentIndex + 1) % item.mediaFiles.length;
        console.log('Next index:', nextIndex);
        this.setActiveMedia(item, nextIndex);
        }
    },
    setActiveMedia(item, index) {
        item.mediaFiles.forEach((media, idx) => {
        media.isActive = idx === index;
        console.log(`Media at index ${idx} active status: `, media.isActive);
        });
    },
    goToPrevPage() {
      // 
      if (this.currentPage > 1) {
        this.currentPage--;
        console.log("Current page after prev:", this.currentPage);
      }
    },
    goToNextPage() {
      // 
        if (this.currentPage < this.totalPages) {
            this.currentPage++;
            console.log("Current page after next:", this.currentPage);
        }
    },
    // 
    resetPage() {
        this.currentPage = 1;
    },
  },

  watch: {
    // 
    filteredItems(newValue, oldValue) {
        if (newValue !== oldValue) {
            this.resetPage();
        }
    }
  },
  mounted() {
    // 
    //  showSuccessModal
    this.filteredItems = this.items; // 
  },
};
</script>

