<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  data() {
    return {
      items: [
      {
        id: 1,
        name: "",
        price: "99",
        description: "",
        kind: '',
        mediaFiles: [
          { url: require('@/assets/img/buyer/food-1.jpg'), isActive: true, isVideo: false },
          { url: require('@/assets/img/buyer/food-2.jpg'), isActive: false, isVideo: false },
        ],
      },
     ],
     searchQuery: '',
      selectedProduct: {
        // 
        name: '',
        description: '',
        price: '99',
        kind: '',
        stock: '13',
        mediaFiles: [
          { url: require('@/assets/img/buyer/food-1.jpg'), isActive: true, isVideo: false },
          { url: require('@/assets/img/buyer/food-2.jpg'), isActive: false, isVideo: false },
          // ... 
        ]
      },
     selectedCategory: '', // 
     filteredItems: [], // 
      currentPage: 1,
      itemsPerPage: 2,
      search: {
        keyword: '',
        kind: ''
      }
    };
  },
  computed: {
    ...mapGetters(['isLoggedIn', 'isSeller', 'isBuyer']),
    username() {
      //  Vuex store 
      return this.$store.state.admin ? this.$store.state.admin.username : '';
    },
    paginatedItems() {
      // 
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = this.currentPage * this.itemsPerPage;
      return this.filteredItems.slice(start, end);
    },
    // 
    totalPages() {
      return Math.ceil(this.filteredItems.length / this.itemsPerPage);
    }
  },
  methods: {
    ...mapActions(['logout']),
    handleLogout() {
      this.logout();
      this.$router.push('/');
    },
    navigateTo(routeName) {
      this.$router.push({ name: routeName });
    },
    beforeMount() {
      if (!this.isLoggedIn) {
        this.$router.push('/');
      }
    },
    fetchProductData() {
      //  API 
      // 
    },
    getUsername() {
      // 
      //  Vuex Vuex 
      return this.$store.state.username;
    },
    showPrevMedia(product) {
        const currentIndex = product.mediaFiles.findIndex(media => media.isActive);
        console.log('Current active index:', currentIndex);

        if (currentIndex >= 0) {
        const prevIndex = (currentIndex - 1 + product.mediaFiles.length) % product.mediaFiles.length;
        console.log('Previous index:', prevIndex);
        this.setActiveMedia(product, prevIndex);
        }
    },
    showNextMedia(product) {
        const currentIndex = product.mediaFiles.findIndex(media => media.isActive);
        console.log('Current active index:', currentIndex);

        if (currentIndex >= 0) {
        const nextIndex = (currentIndex + 1) % product.mediaFiles.length;
        console.log('Next index:', nextIndex);
        this.setActiveMedia(product, nextIndex);
        }
    },
    setActiveMedia(item, index) {
        item.mediaFiles.forEach((media, idx) => {
        media.isActive = idx === index;
        console.log(`Media at index ${idx} active status: `, media.isActive);
        });
    },
    buyProduct(productId) {
      // 
      this.$router.push({ name: 'BuyerFillInfo', params: { productId } });
    },
    addToCart() {
      alert("");
      // 
    },
    addToFavorites() {
      alert("");
      // 
    }
  },
  created() {
    //  fetchProductData methods 
    this.fetchProductData();
    this.username = this.getUsername(); // 
  },
  mounted() {
    // 
    //  showSuccessModal
    this.filteredItems = this.items; // 
  },
};
</script>

