<template>

  <body style="margin: 0px;">
    <div v-if="isLoggedIn">
      <div class="left">
        <!--  -->
        <table class="daohang">
          <img class="head1" src="~@/assets/img/buyer/head.png" alt="">
          <tr>
            <td class="head2">{{ username }}</td>
          </tr>
          <tr>
            <td class="head4">
              <h3 @click="navigateTo('BuyerCart')" class="head4-1" style="cursor: pointer;"></h3>
            </td>
          </tr>
          <tr>
            <td class="head4">
              <h3 @click="navigateTo('BuyerLikes')" class="head4-1" style="cursor: pointer;"></h3>
            </td>
          </tr>
          <tr>
            <td class="head4">
              <h3 @click="navigateTo('buyerHistory')" class="head4-1" style="cursor: pointer;"></h3>
            </td>
          </tr>
          <tr>
            <td class="head4">
              <h3 @click="navigateTo('BuyerMain')" class="head4-1" style="cursor: pointer;"></h3>
            </td>
          </tr>
          <tr>
            <td class="head5">
              <button @click="handleLogout" class="head5-1" style="cursor: pointer;"></button>
            </td>
          </tr>
        </table>
      </div>
      <div class="right" style="width: 50%;">
        <div class="container">
          <div class="centered-container">
            <h2></h2>
          </div>
          <table border="1px" align=center cellspacing="0" style="width:90%;">
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>

            <tr v-for="item in paginatedItems" :key="item.GOODID">
              <td class="table-column">
                <div style="display: flex; justify-content: center; align-items: center;">
                  <div style="text-align: center;">
                    <div class="media-container" :class="{ 'sold-out': item.state !== 0 }">
                      <div v-show="item.PICTURE !== ''">
                        <img :src="item.PICTURE" alt="">
                      </div>
                    </div>
                    <!-- <div class="media-navigation"> -->
                    <!-- <button @click="showPrevMedia(item)"></button>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                      <button @click="showNextMedia(item)"></button> -->
                    <!-- </div> -->
                    <div v-if="item.state !== 0" class="overlay"></div>
                  </div>
                </div>
              </td>
              <td class="table-column">
                <div class="centered-container">{{ item.GOODNAME }}</div>
              </td>
              <td class="table-column">
                <div class="centered-container">{{ item.DESCRIPTION }}</div>
              </td>
              <td>
                <div class="centered-container">
                  <button class="delete-button" @click="dislike(item.GOODID, 1)"></button>
                </div>
              </td>
            </tr>
          </table>
          <div class="pagination">
            <button @click="goToPrevPage" class="prev" :disabled="currentPage === 1"></button>
            <span id="page-info"> {{ currentPage }} / {{ totalPages }} </span>
            <button @click="goToNextPage" class="next" :disabled="currentPage === totalPages"></button>
          </div>
        </div>
      </div>
    </div>
    <div v-else class="else">
      <a href="login"></a>
    </div>
  </body>
</template>

<script>
import axios from 'axios';
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
import { mapGetters, mapActions } from 'vuex';

export default {
  data() {
    return {
      items: [
        // ...
      ],
      searchQuery: '',
      selectedCategory: '', // 
      filteredItems: [], // 
      currentPage: 1,
      itemsPerPage: 2,
      search: {
        keyword: '',
        kind: ''
      },
      isLoggedIn: true,
      currentUser: {},
    };
  },
  computed: {
    ...mapGetters(['isLoggedIn', 'isSeller', 'isBuyer']),
    username() {
      //  Vuex store 
      return this.$store.state.admin ? this.$store.state.admin.username : '';
    },
    paginatedItems() {
      // 
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = this.currentPage * this.itemsPerPage;
      return this.items.slice(start, end);
    },
    // 
    totalPages() {
      return Math.ceil(this.items.length / this.itemsPerPage);
    }
  },
  created() {
    this.fetchUsrFromSession();
  },
  methods: {
    ...mapActions(['logout']),
    dislike(itemId, iscancel) {
      // 
      axios.post('/good/addLike', { goodid: itemId, iscancel: iscancel, userid: this.currentUser.userid }).then(res => {
        console.log(res);
        alert("");
        this.getLiskLIst(this.currentUser.userid);
        // 
      }).catch(error => {
        // 
        console.error(error);
      });
    },
    handleLogout() {
      this.logout();
      this.$router.push('/');
    },
    navigateTo(routeName) {
      this.$router.push({ name: routeName });
    },
    beforeMount() {
      if (!this.isLoggedIn) {
        this.$router.push('/');
      }
    },
    async fetchUsrFromSession() {
      try {
        //  GET 
        const response = await axios.get('/now-usr');

        // 
        const usr = response.data;

        //  currentUser 
        this.currentUser = usr;
        this.getLiskLIst(this.currentUser.userid);
        return true;
      } catch (error) {
        console.error(':', error);
        return false;
      }
    },
    getLiskLIst(userid) {
      // 
      axios.post('/good/showLike', { userId: userid }).then(res => {
        // 
        this.items = res.data;
        this.items.forEach(item => {
          console.log(item);
        });
      }).catch(error => {
        // 
        console.error(error);
      });
    },
    handleSearch() {
      this.items = this.items.filter(item => {
        const matchesKeyword = this.searchQuery ? item.name.toLowerCase().includes(this.searchQuery.toLowerCase()) : true;
        const matchesCategory = this.selectedCategory ? item.kind === this.selectedCategory : true;

        console.log(`Item: ${item.name}, Keyword Match: ${matchesKeyword}, Category Match: ${matchesCategory}`);

        return matchesKeyword && matchesCategory;
      });
      console.log("Filtered items count:", this.items.length);
      // 
    },
    showPrevMedia(item) {
      const currentIndex = item.mediaFiles.findIndex(media => media.isActive);
      console.log('Current active index:', currentIndex);

      if (currentIndex >= 0) {
        const prevIndex = (currentIndex - 1 + item.mediaFiles.length) % item.mediaFiles.length;
        console.log('Previous index:', prevIndex);
        this.setActiveMedia(item, prevIndex);
      }
    },
    showNextMedia(item) {
      const currentIndex = item.mediaFiles.findIndex(media => media.isActive);
      console.log('Current active index:', currentIndex);

      if (currentIndex >= 0) {
        const nextIndex = (currentIndex + 1) % item.mediaFiles.length;
        console.log('Next index:', nextIndex);
        this.setActiveMedia(item, nextIndex);
      }
    },
    setActiveMedia(item, index) {
      item.mediaFiles.forEach((media, idx) => {
        media.isActive = idx === index;
        console.log(`Media at index ${idx} active status: `, media.isActive);
      });
    },
    goToPrevPage() {
      // 
      if (this.currentPage > 1) {
        this.currentPage--;
        console.log("Current page after prev:", this.currentPage);
      }
    },
    goToNextPage() {
      // 
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
        console.log("Current page after next:", this.currentPage);
      }
    },
    // 
    resetPage() {
      this.currentPage = 1;
    },
  },
  watch: {
    // 
    items(newValue, oldValue) {
      if (newValue !== oldValue) {
        this.resetPage();
      }
    }
  },
  mounted() {
    // 
    //  showSuccessModal
    this.filteredItems = this.items; // 
  },
};
</script>

