<template>
  <div class="success-page">
    <h2></h2>
    <p v-if="message">{{ message }}</p>

    <router-link v-if="to === '/'" to="/"></router-link>
    <router-link v-if="to === 'buyermain'" to="/buyer"></router-link>
    <router-link v-if="to === 'show_goods'" to="/seller/ShowGoods"></router-link>
    <router-link v-if="to === 'show_pwd'" to="/"></router-link>

    <div v-if="isLoggedIn">
      <router-link v-if="to === 'good'" to="/good"></router-link>
      <router-link v-if="to === 'createorder'" to="/create-order"></router-link>

      <div v-if="isSeller">
        <router-link v-if="to === 'changepwd'" to="/seller/change-pwd"></router-link>
        <router-link v-if="to === 'order'" to="/seller/order"></router-link>
        <router-link v-if="to === 'upload_goods'" to="/seller/upload-goods"></router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  computed: {
    ...mapGetters(['isLoggedIn', 'isSeller']),
    message() {
      //  Vuex 
      return this.$route.params.message || '';
    },
    to() {
      // 
      return this.$route.params.to || '/';
    },
  },
  mounted() {
    console.log("Message:", this.message);
    console.log("To:", this.to);
    }
};
</script>

