<template>
    <ul class="daohang">
      <li><h2>""!</h2></li>
      <div class="cat-background"></div>
    </ul>

    <div v-if="!isLoggedIn" class="login">
      <form @submit.prevent="handleLogin">
        <div class="input">
          <img src="~@/assets/img/account.png" alt="" style="height: 25px; vertical-align: middle;"> 
          <input type="text" name="username" placeholder="" v-model="credentials.username">
        </div>
        <div class="input">
          <img src="~@/assets/img/password.png" alt="" style="height: 25px; vertical-align: middle;"> 
          <input type="password" name="password" placeholder="" v-model="credentials.password">
        </div>
        <router-link to="/forgot-password" class="forgot-password">?</router-link>
        <div class="image-buttons">
          <button class="butt-1" type="submit"></button>
          <router-link to="/register" class="butt-1"></router-link>
          <router-link to="/buyer" class="butt-1"></router-link>
        </div>
      </form>
    </div>
    <div v-else class="else">
      !!!
      <router-link :to="adminRoute"></router-link>
    </div>
    <div id="b">
        <div v-if="error" class="error">{{ error }}</div>
    </div>
</template>



<script>
import axios from 'axios';
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

export default {
  data() {
    return {
      credentials: {
        username: '',
        password: ''
      },
      error: '',
      mockAdminData: null,
      currentUser :null
    };
  },

  created() {
    this.fetchUsrFromSession();
  },
  computed: {
    isLoggedIn() {
      // 
      return !!this.currentUser;
    },
    isSeller() {
      // 
      return this.currentUser && this.currentUser.power === 1;
    },
    // 
    isBuyer() {
      // 
      return this.currentUser && this.currentUser.power === 0;
    },
    adminRoute() {
      // 
      return this.isSeller ? '/seller' : '/buyer';
    }

  },
  methods: {
    async fetchUsrFromSession() {
      try {
        //  GET 
        const response = await axios.get('/now-usr');
        // 
        const usr = response.data;
        //  currentUser 
        this.currentUser = usr;

        return true;
      } catch (error) {
        console.error(':', error);
        return false;
      }
    },
    async handleLogin() {
      try {
        const credentials = {
          username: this.credentials.username,
          password: this.credentials.password
        };

        const response = await fetch('/usr/login-control', {
          method: 'POST',
          body: JSON.stringify(credentials),
          headers: {
            'Content-Type': 'application/json'
          }
        });

        const responseData = await response.json();

        if (responseData.page === 'error') {
          // 
          this.$router.push({ path: '/error', query: { err: responseData.msg, to: responseData.data }})
        } else if (responseData.page === 'success') {
          // 
          this.$router.push({ path: '/success', query: { message: responseData.msg, to: responseData.data }})
        } else if (responseData.page === 'buyer') {// 
            // 
            this.$router.push({ name: 'BuyerMain' });
          } else if (responseData.page === 'seller') {
            // 
            this.$router.push({ name: 'SellerMain' });
          } else {
          // 
          console.log("");
        }
      } catch (error) {
        console.error(":", error);
        this.error = '';
      }
    },

    async handleLogout() {
      try {
        // 
        await axios.get('/logout-control');
        // 
        this.$router.push('/');
      } catch (error) {
        console.error(':', error);
        // 
      }
    },
  },
}
</script>



