<template>
  <div v-if="isLoggedIn">
    <div id="a">
    <div class="container">
      <div class="form-group">
        <h2></h2>
      </div>
      <table border="1px" align=center cellspacing="0">
        <tr>
          <th>ID</th>
          <th></th>
          <th></th>
          <th></th>
          <th>ID</th>
          <th></th>
          <th></th>
        </tr>
        <tr v-for="(order, index) in paginatedItems" :key="index">
          <td>{{ order.orderid }}</td>
          <td>{{ order.address }}</td>
          <td>{{ order.telephone }}</td>
          <td>{{ order.buyername }}</td>
          <td>{{ order.goodid }}</td>
          <td>
            <button v-if="order.orderstate < 4 && order.orderstate > 0" @click="confirmOrder(order.orderid)" class="green-btn"></button>
            <button v-if=" order.orderstate <= 4 && order.orderstate > 0" @click="cancelOrder(order.orderid)" class="red-btn"></button>
            <span v-if="order.orderstate < 0 || order.orderstate > 4"></span>
          </td>
          <td>
            {{ getOrderStatus(order.orderstate) }}
          </td>
        </tr>
      </table>
      <div class="pagination">
        <button @click="goToPrevPage" :disabled="isPrevDisabled" class="prev"></button>
        <span>{{ currentPage }}/{{ totalPages }}</span>
        <button @click="goToNextPage" :disabled="isNextDisabled" class="next"></button>
      </div>
    </div>
    </div>
  </div>
  <div v-else class="else">
    <a href="login"></a>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isLoggedIn: true, // 
      orders: [
        { orderid: 1, address: '1', telephone: '1234567890', buyername: 'A', goodid: 101, orderstate: -1 },
        { orderid: 2, address: '2', telephone: '0987654321', buyername: 'B', goodid: 102, orderstate: 1 },
        { orderid: 3, address: '3', telephone: '1122334455', buyername: 'C', goodid: 103, orderstate: 2 },
        { orderid: 4, address: '4', telephone: '5566778899', buyername: 'D', goodid: 104, orderstate: 3 },
        { orderid: 5, address: '5', telephone: '1231231234', buyername: 'E', goodid: 105, orderstate: 4 },
        { orderid: 6, address: '6', telephone: '3344556677', buyername: 'F', goodid: 106, orderstate: 5 },
        // ... 
      ],
      currentPage: 1,
      itemsPerPage: 5,
      //  totalItems 
      totalItems: 6, 
    };
  },
  methods: {
    getOrderStatus(state) {
      // 
      // 
      const statusMap = {
        0: '',
        1: '',
        2: '',
        3: '',
        4: '',
        5: '', //  5 
        // 
      };
      return statusMap[state] || '';
    },
    handleOrderAction(orderId, action) {
      //  action 
      switch(action) {
        case 'wait':
          // 
          break;
        case 'confirm':
          this.confirmOrder(orderId);
          break;
        case 'prepare':
          // 
          break;
        case 'ship':
          // 
          break;
        case 'cancel':
          this.cancelOrder(orderId);
          break;
        // ...
      }
    },
    confirmOrder(orderId) {
      const order = this.orders.find(o => o.orderid === orderId);
      if (order) {
        order.orderstate += 1; //  1
        // 
      }
      alert("");
      // 
      //  axios  fetch
      console.log("", orderId);
      // 
    },
    cancelOrder(orderId) {
      const order = this.orders.find(o => o.orderid === orderId);
      if (order) {
        order.orderstate = -1; //  -1
        // 
      }
      alert("");
      // 
      //  axios  fetch
      console.log("", orderId);
      // 
    },
    goToPrevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
        this.fetchOrders(); // 
      }
    },
    goToNextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
        this.fetchOrders(); // 
      }
    },
    fetchOrders() {
      // 
      // ...
    }
  },
  computed: {
    totalPages() {
      return Math.ceil(this.totalItems / this.itemsPerPage);
    },
    isPrevDisabled() {
      return this.currentPage <= 1;
    },
    isNextDisabled() {
      return this.currentPage >= this.totalPages;
    },
    filteredOrders() {
      //  -1 
      return this.orders.filter(order => order.orderstate !== -2);
    },
    paginatedItems() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = this.currentPage * this.itemsPerPage;
      return this.orders.slice(start, end);
    },
  },
  created() {
    this.fetchOrders(); // 
  },
}
</script>

