<template>

  <body style="margin: 0px;">
    <div v-if="isLoggedIn">
      <div class="left">
        <!--  -->
        <table class="daohang">
          <img class="head1" src="~@/assets/img/buyer/head.png" alt="">
          <tr>
            <td class="head2">{{ username }}</td>
          </tr>
          <tr>
            <td class="head4">
              <h3 @click="navigateTo('BuyerCart')" class="head4-1" style="cursor: pointer;"></h3>
            </td>
          </tr>
          <tr>
            <td class="head4">
              <h3 @click="navigateTo('BuyerLikes')" class="head4-1" style="cursor: pointer;"></h3>
            </td>
          </tr>
          <tr>
            <td class="head4">
              <h3 @click="navigateTo('buyerHistory')" class="head4-1" style="cursor: pointer;"></h3>
            </td>
          </tr>
          <tr>
            <td class="head4">
              <h3 @click="navigateTo('BuyerMain')" class="head4-1" style="cursor: pointer;"></h3>
            </td>
          </tr>
          <tr>
            <td class="head5">
              <button @click="handleLogout" class="head5-1" style="cursor: pointer;"></button>
            </td>
          </tr>
        </table>
      </div>

      <div class="right" style="width: 60%; margin: 0px;">
        <div class="container">
          <div class="centered-container">
            <h2></h2>
          </div>
          <button id="editall-button" @click="toggleEdit">{{ editButtonText }}</button>

          <table border="1px" align=center cellspacing="0" style="width:100%;">
            <tr>
              <th class="table-column"></th>
              <th class="table-column"></th>
              <th class="table-column"></th>
              <th class="table-column"></th>
              <th class="table-column"></th>
              <th class="table-column"></th>
            </tr>
            <tr v-for="item in visibleItems" :key="item.id">

              <td class="table-column">
                <div style="display: flex; justify-content: center; align-items: center;">
                  <div style="text-align: center;">
                    <div class="media-container" :class="{ 'sold-out': item.state !== 0 }">
                      <div v-show="item.MEDIAFILES !== ''">
                        <img :src="item.MEDIAFILES" alt="">
                      </div>
                    </div>
                    <!-- <div class="media-navigation">
                      <button @click="showPrevMedia(item)"></button>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                      <button @click="showNextMedia(item)"></button>
                    </div> -->
                    <div v-if="item.state !== 0" class="overlay"></div>
                  </div>
                </div>
              </td>
              <td class="table-column">
                <div class="centered-container">{{ item.NAME }}</div>
              </td>
              <td class="table-column">
                <div class="centered-container">
                  <button v-if="isEditing" @click="decrementQuantity(item)">-</button>
                  <input type="number" v-model.number="item.quantity" :disabled="!isEditing" style="width:64.6px;">
                  <button v-if="isEditing" @click="incrementQuantity(item)">+</button>
                </div>
              </td>
              <td class="table-column">
                <div class="centered-container">{{ item.PRICE * item.quantity }}</div>
              </td>
              <td class="table-column">
                <div class="centered-container">
                  <button @click="addToFavorites(item.ID, 0)"></button>
                  <button v-if="isEditing" @click="removeFromCart(item.ID)"></button>
                </div>
              </td>
              <td class="table-column">
                <div class="centered-container">
                  <input type="checkbox" v-model="selectedItems[item.id]">
                </div>
              </td>
            </tr>
          </table>
          <div>
            <h3>: {{ selectedTotalPrice }}</h3>
            <button @click="submitSelectedOrders"></button>
            <button @click="addToFavoritesSelectedItems"></button>
          </div>
          <div class="pagination">
            <button @click="goToPrevPage" :disabled="isPrevDisabled" class="prev"></button>
            <span>{{ currentPage }} / {{ totalPages }}</span>
            <button @click="goToNextPage" :disabled="isNextDisabled" class="next"></button>
          </div>
        </div>
      </div>
    </div>
    <div v-else class="else">
      <a href="login"></a>
    </div>
  </body>
</template>

<script>
import axios from 'axios';
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
import { mapGetters, mapActions } from 'vuex';
export default {
  data() {
    return {
      items: [
        // ...
      ],
      favorites: [], // 
      searchQuery: '',
      selectedCategory: '', // 
      search: {
        keyword: '',
        kind: ''
      },
      isLoggedIn: true, // 
      selectedItems: {}, // ID
      currentPage: 1,
      itemsPerPage: 2,
      isEditing: true,
      currentUser: {}
    };
  },
  computed: {
    ...mapGetters(['isLoggedIn', 'isSeller', 'isBuyer']),
    visibleItems() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.items.slice(start, end);
    },
    username() {
      //  Vuex store 
      return this.$store.state.admin ? this.$store.state.admin.username : '';
    },
    paginatedItems() {
      // 
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = this.currentPage * this.itemsPerPage;
      return this.filteredItems.slice(start, end);
    },
    // 
    totalPages() {
      return Math.ceil(this.filteredItems.length / this.itemsPerPage);
    },
    selectedTotalPrice() {
      return Object.keys(this.selectedItems).reduce((total, itemId) => {
        const item = this.cartItems.find(item => item.ID === Number(itemId));
        if (item && this.selectedItems[itemId]) {
          return total + item.PRICE * item.quantity;
        }
        return total;
      }, 0);
    },
    editButtonText() {
      return this.isEditing ? '' : '';
    },
  },
  created() {
    this.cartItems = this.items;
    this.filteredItems = this.cartItems; //  filteredItems 
    this.fetchCartItems(); // 
    this.fetchCartItems(); // 
    this.fetchUsrFromSession();
  },
  methods: {
    ...mapActions(['logout']),
    isItemVisible(index) {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return index >= start && index < end;
    },
    async fetchUsrFromSession() {
      try {
        //  GET 
        const response = await axios.get('/now-usr');

        // 
        const usr = response.data;

        //  currentUser 
        this.currentUser = usr;
        this.getLIst(this.currentUser.userid)
        return true;
      } catch (error) {
        console.error(':', error);
        return false;
      }
    },
    getLIst(userid) {
      // 
      axios.get('/good/allCart?userId=' + userid).then(res => {
        // 
        this.items = res.data;
        // this.isEditing = res.data.MAXQUANTITY > 0 ? true : false;
      }).catch(error => {
        // 
        console.error(error);
      });
    },
    handleLogout() {
      this.logout();
      this.$router.push('/');
    },
    navigateTo(routeName) {
      this.$router.push({ name: routeName });
    },
    beforeMount() {
      if (!this.isLoggedIn) {
        this.$router.push('/');
      }
    },
    fetchCartItems() {
      // AJAX 
      // this.cartItems = fetch('/api/cart').then(response => response.json());
    },
    toggleEdit() {
      this.isEditing = !this.isEditing;
      if (!this.isEditing) {
        this.updateCart(); // 
      }
    },
    incrementQuantity(item) {
      if (item.quantity < item.MAXQUANTITY) {
        item.quantity++;
      }
    },
    decrementQuantity(item) {
      if (item.quantity > 1) {
        item.quantity--;
      }
    },
    removeFromCart(itemId) {
      // 
      axios.post('/good/delete-cart-item', { buyingid: itemId, userid: this.currentUser.userid })
        .then(res => {
          // 
          this.items = res.data;
          this.getLIst(this.currentUser.userid)
          alert("");
        })
        .catch(error => {
          // 
          console.error(error);
        });
    },
    addToFavorites(itemId, iscancel) {
      // 
      axios.post('/good/addLike', { goodid: itemId, iscancel: iscancel, userid: this.currentUser.userid }).then(res => {
        alert("");
        console.log(res)
        this.getLIst(this.currentUser.userid)
        // 
      }).catch(error => {
        // 
        console.error(error);
      });
    },
    submitSelectedOrders() {
      this.items = this.items.filter(item => !this.selectedItems[item.ID]);
      this.selectedItems = {};
      alert("");
    },
    addToFavoritesSelectedItems() {
      // 
      alert("");
      // 
      this.selectedItems = {};
    },
    changePage(newPage) {
      this.currentPage = newPage;
    },
    updateCart() {
      // 
    },
    showPrevMedia(item) {
      const currentIndex = 0;
      console.log('Current active index:', currentIndex);

      if (currentIndex >= 0) {
        const prevIndex = (currentIndex - 1 + item.MEDIAFILES.length) % item.MEDIAFILES.length;
        console.log('Previous index:', prevIndex);
        this.setActiveMedia(item, prevIndex);
      }
    },
    showNextMedia(item) {
      const currentIndex = 0;
      console.log('Current active index:', currentIndex);

      if (currentIndex >= 0) {
        const nextIndex = (currentIndex + 1) % item.MEDIAFILES.length;
        console.log('Next index:', nextIndex);
        this.setActiveMedia(item, nextIndex);
      }
    },
    setActiveMedia(item, index) {
      item.mediaFiles.forEach((media, idx) => {
        media.isActive = idx === index;
        console.log(`Media at index ${idx} active status: `, media.isActive);
      });
    },
    goToPrevPage() {
      // 
      if (this.currentPage > 1) {
        this.currentPage--;
        console.log("Current page after prev:", this.currentPage);
      }
    },
    goToNextPage() {
      // 
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
        console.log("Current page after next:", this.currentPage);
      }
    },
    // 
    resetPage() {
      this.currentPage = 1;
    },
  },

  watch: {
    // 
    filteredItems(newValue, oldValue) {
      if (newValue !== oldValue) {
        this.resetPage();
      }
    }
  },
  mounted() {
    // 
    //  showSuccessModal
    this.filteredItems = this.items; // 
  },
}
</script>

<style scoped>
