<template>
  <div v-if="isLoggedIn">
    <div id="a">
    <div class="container">
      <div class="centered-container">
        <h2></h2><br>
      </div>
      <div class="centered-container">
      <div class="search-bar">
        <input type="text" v-model="searchQuery" class="custom-input" placeholder="">
        <select v-model="selectedCategory"  id="search_kind">
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
        </select>
        <button @click="searchGoods" style="width:130px" class="custom-button"></button>
      </div>
      </div>

      <table border="1px" align=center cellspacing="0">
        <tr>
          <th>ID</th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr v-for="good in paginatedGoods" :key="good.goodid">
            <td>{{ good.goodid }}</td>
            <td>{{ good.goodname }}</td>
            <td>{{ good.description }}</td>
            <td>{{ good.price }}</td>
            <td>
              <div class="media-container">
                <div v-for="(media, index) in good.mediaFiles" :key="index" v-show="media.isActive">
                  <img v-if="!isVideo(media)" :src="media.url" alt="" v-show="media.isActive">
                  <video v-if="isVideo(media)" :src="media.url" controls v-show="media.isActive"></video>
                </div>
              </div>
              <div class="media-navigation">
                <button @click="showPrevMedia(good)"></button>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <button @click="showNextMedia(good)"></button>
              </div>
            </td>
            <td>{{ good.kind }}</td>
            <td>{{ good.subkind }}</td>
            <td>{{ good.createdate }}</td>
        </tr>
      </table>

      <!--  -->
      <div class="pagination">
        <button @click="goToPrevPage" class="prev" :disabled="currentPage === 1"></button>
        <span id="page-info"> {{ currentPage }} / {{ totalPages }} </span>
        <button @click="goToNextPage" class="next" :disabled="currentPage === totalPages"></button>
      </div>
    </div>
    </div>
  </div>
  <div v-else class="else">
    <a href="/login"></a>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      products: [],
      selectedGood: null, // 
      selectedCategory: '', // 
      currentPage: 1,
      pageSize: 6, // 
      goods: [], // 
      filteredGoods: [], // 
      currentUser:null,
      isHistory: 1,
    };
  },
  created() {
    this.fetchUsrFromSession();
    this.fetchgoodListSession();
  },
  computed: {
    isLoggedIn() {
      // 
      return !!this.currentUser;
    },
    paginatedGoods() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.filteredGoods.slice(start, end);
    },
    // 
    totalPages() {
      return Math.ceil(this.filteredGoods.length / this.pageSize);
    }
  },
  async mounted() {
    console.log('ShowHistoryGoods ');
    await this.fetchgoodListSession(); //  fetchgoodListSession !!
    // 
    //  showSuccessModal
    this.filteredGoods = this.goods; // 
    console.log('this.filteredGoods:', this.filteredGoods);
  },
  methods: {
    async fetchUsrFromSession() {
      try {
        //  GET 
        const response = await axios.get('/now-usr');
        // 
        const usr = response.data;
        //  currentUser 
        this.currentUser = usr;

        return true;
      } catch (error) {
        console.error(':', error);
        return false;
      }
    },
    async fetchgoodListSession() {
      try {
        //  GET 
        const goodsResponse = await axios.get('/good/seller-all-historygood-list-control');
        // 
        // console.log('goodList:', goodsResponse);
        const goodList = goodsResponse.data.data;//goodsResponsedata
        //  products 
        // picturemediaFiles
        //  console.log('goodList:', goodList);

        this.goods = goodList.map( good => {
          //    console.log('Before trimming:', good.picture); // 
          const trimmedPicture = good.picture.trim();
          //     console.log('After trimming:', trimmedPicture); // 
          const paths = trimmedPicture.split(',');
          const mediaFiles = paths.map((path, i) => {
            return {
              url: path,
              isActive: i === 0 // truefalse
            };
          });
          return {
            ...good,
            mediaFiles,
            // 
            goodname: good.goodname.trim(),
            description: good.description.trim(),
            price: good.price,
            number: good.number,
            kind: good.kind,
            subkind: good.subkind,
            createdate: good.createdate
          };
        });
        console.log('this.goods:', this.goods);
        return true;
      } catch (error) {
        console.error(':', error);
        return false;
      }
    },
    showPrevMedia(good) {
      const currentIndex = good.mediaFiles.findIndex(media => media.isActive);
      console.log('Current active index:', currentIndex);

      if (currentIndex >= 0) {
        const prevIndex = (currentIndex - 1 + good.mediaFiles.length) % good.mediaFiles.length;
        console.log('Previous index:', prevIndex);
        this.setActiveMedia(good, prevIndex);
      }
    },
    showNextMedia(good) {
      const currentIndex = good.mediaFiles.findIndex(media => media.isActive);
      console.log('Current active index:', currentIndex);

      if (currentIndex >= 0) {
        const nextIndex = (currentIndex + 1) % good.mediaFiles.length;
        console.log('Next index:', nextIndex);
        this.setActiveMedia(good, nextIndex);
      }
    },
    isVideo(media) {
      const isMediaVideo = media.url.endsWith('.mp4');
      console.log(`Is media a video: ${isMediaVideo}`);
      return isMediaVideo;
    },
    searchGoods() {
        console.log('');
        this.filteredGoods = this.goods.filter((good) => {
            const matchesQuery = this.searchQuery ? good.goodname.toLowerCase().includes(this.searchQuery.toLowerCase()) : true;
            const matchesCategory = this.selectedCategory ? good.kind === this.selectedCategory : true;
            this.resetPage();
            return matchesQuery && matchesCategory;
        });
        // 
    },
    goToPrevPage() {
      // 
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    goToNextPage() {
      // 
        if (this.currentPage < this.totalPages) {
            this.currentPage++;
        }
    },
    // 
    resetPage() {
        this.currentPage = 1;
    },
    fetchProducts() {
      // 
    }
  },

  watch: {
    // 
    filteredGoods(newValue, oldValue) {
        if (newValue !== oldValue) {
            this.resetPage();
        }
    }
  },

};
</script>

