<template>
  <div v-if="isLoggedIn">
    <!--  -->
    <ul class="daohang">
      <li><b>""!</b></li>
      <li style="float:right" @click="handleLogout" class="logout-button"></li>
    </ul>
    
    <!--  -->
    <div class="content">
      <div class="content-left">
        <!---->
        <div class="nav">
          <!---->
          <div class="nav-menu">
            <!---->
            <div class="nav-info">
              <div class="user-image" style="left: -40px;">
                <img src="@/assets/img/catbackground.png" alt="" style="width: 50px;height: 50px;left: 8.9%;position: absolute;"/>
              </div>
              <div class="username" style="margin-right: 100px;">{{ getUsername }}</div>
              <img src="~@/assets/img/line.png" alt="" class="separator-line"><!--  -->
            </div>
            <!---->
            <div class="nav-content">
              <!--  -->
              <h3 @click="navigateTo('UpdatePassword')" class="black-text" style="cursor: pointer;"></h3>
              <h3 @click="navigateTo('UploadOneGood')" class="black-text" style="cursor: pointer;"></h3>
              <h3 @click="navigateTo('ShowUserInfo')" class="black-text" style="cursor: pointer;"></h3>
              <h3 @click="navigateTo('UploadMultipleGoods')" class="black-text" style="cursor: pointer;"></h3>
              <h3 @click="navigateTo('ShowHistoryGoods')" class="black-text" style="cursor: pointer;"></h3>
              <h3 @click="navigateTo('ShowGoods')" class="black-text" style="cursor: pointer;"></h3>
              <h3 @click="navigateTo('ShowAllUsers')" class="black-text" style="cursor: pointer;"></h3>
              <!--  -->
            </div>
          </div>
        </div>
      </div>

      <!--  -->
      <div>
        <!--  -->
        <div class="my">
          <router-view></router-view> <!--  -->
        </div>
      </div>
    </div>
  </div>
   <div v-else>
    <!--  -->
    <div class="else">
      <a href="/"></a>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
    data() {
        return {
          // 
          currentUser :null
        };
    },
    created() {
      this.fetchUsrFromSession();
    },
    computed: {
      isLoggedIn() {
        // 
        return !!this.currentUser;
      },
      isSeller() {
        // 
        return this.currentUser && this.currentUser.power === 1;
      },
      // 
      isBuyer() {
        // 
        return this.currentUser && this.currentUser.power === 0;
      },
      getUsername() {
        // 
        return this.currentUser ? this.currentUser.username : '';
      }
    },
    methods: {
        async fetchUsrFromSession() {
          try {
            //  GET 
            const response = await axios.get('/now-usr');
            // 
            const usr = response.data;
            //  currentUser 
            this.currentUser = usr;
            // console.log(this.currentUser);
            // console.log(!this.isLoggedIn);
            // console.log(!this.isSeller);
            // console.log(!this.isLoggedIn || !this.isSeller);
            // console.log("ss");
            if (!this.isLoggedIn || !this.isSeller) {
              this.$router.push('/');
            }
            return true;
          } catch (error) {
            console.error(':', error);
            return false;
          }
        },
        async handleLogout() {
        try {
          // 
          await axios.get('/logout-control');
          // 
          this.$router.push('/');
        } catch (error) {
          console.error(':', error);
          // 
        }
      },
        navigateTo(routeName) {
            this.$router.push({ name: routeName });
        },
    },
    beforeMount() {

    },
    mounted() {
      console.log('SellerMain ');
    },
};
</script>



