<template>
  <div>
    <h2></h2>
    <div v-if="errorMessage">
      {{ errorMessage }}
    </div>
    <div v-if="redirectTo">
      <router-link :to="redirectTo"></router-link>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      errorMessage: '',
      redirectTo: null
    };
  },
  created() {
    // 
    this.errorMessage = this.$route.query.err || '';
    this.redirectTo = this.$route.query.to || '/';
  }
};
</script>

